@import '../../variables.scss';
@import '../../mixins.scss';

.assigment {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../../../public/images/bg.jpg);
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  height: 500px;
  display: flex;
  
  @include onDesktop {
    height: 650px;
  }

  &__box-content {
    margin: auto;
    padding: 40px 16px 0;
    text-align: center;
    max-width: 380px;
  }

  &__title, &__text {
    margin: 0 0 21px;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    color: $background-color-header;
  }

  &__title {
    font-size: 40px;
    line-height: 40px;
  }

  &__button {
    display: inline-block;
 }
}
